<template>
    <div class="navigate-btn">
        <div class="before-btn">
            <button
                @click="beforeBtn"
            >Back</button>
        </div>
        <div class="number-page-list"
            v-if="!lastPage"
        >
            <div
                class="number-page"
                v-for="index in 3"
                :key="index"
            >
                <button
                    :class="{ active : currentPage == currentPage + ( index-1 )}"
                    v-if="currentPage + ( index - 1 ) != 0"
                    @click="numPageClick(currentPage + ( index - 1 ))"
                >
                    {{ currentPage + ( index - 1 )}}
            </button>
            </div>
        </div>
        <div class="next-btn"
            v-if="!lastPage"
        >
            <button
                :disabled="!nextPage"
                @click="afterBtn"
            >Next</button>
        </div>
    </div>
</template>

<script>
export default {
    name : 'myNavigateBtn',
    props : {
        currentPage : {
            type : Number,
            default : 0,
        },
        nextPage : {
            type : Boolean,
            default : false,
        },
        lastPage : {
            type : Boolean,
        }
    },
    data() {
        return {
        }
    },
    methods : {
        numPageClick(num) {
            
            this.$emit('gotoPage',num);
        },
        beforeBtn() {
            const currentPage = this.currentPage - 3 < 1  ? 1 : this.currentPage - 1 ;
            this.$emit('gotoPage',currentPage);
        },
        afterBtn() {
            const currentPage = this.currentPage + 3;
            this.$emit('gotoPage', currentPage);
        }
    }
}
</script>

